<template>
	<Example vertical id="example-badge-type">
		<template slot="source">
			<div class="example-badge-type">
				<vui-badge type="default" :count="10">
					<img src="https://dummyimage.com/48x48/e6e6e6/6a6a6a" />
				</vui-badge>
				<vui-badge type="primary" :count="10">
					<img src="https://dummyimage.com/48x48/e6e6e6/6a6a6a" />
				</vui-badge>
				<vui-badge type="info" :count="10">
					<img src="https://dummyimage.com/48x48/e6e6e6/6a6a6a" />
				</vui-badge>
				<vui-badge type="warning" :count="10">
					<img src="https://dummyimage.com/48x48/e6e6e6/6a6a6a" />
				</vui-badge>
				<vui-badge type="success" :count="10">
					<img src="https://dummyimage.com/48x48/e6e6e6/6a6a6a" />
				</vui-badge>
				<vui-badge type="error" :count="10">
					<img src="https://dummyimage.com/48x48/e6e6e6/6a6a6a" />
				</vui-badge>
			</div>
		</template>
		<template slot="title">预设类型</template>
		<template slot="describe">
			<p>使用 <code>type</code> 属性，可以创建不同类型的徽标。默认情况下使用 <code>error</code> 类型。</p>
		</template>
		<template slot="code">{{code}}</template>
	</Example>
</template>

<script>
	import Example from "@/components/example";
	import code from "./code";

	export default {
		name: "ExampleBadgeType",
		components: {
			Example
		},
		data() {
			return {
				code
			};
		}
	};
</script>

<style>
	.example-badge-type > .vui-badge { margin-right:20px; }
	.example-badge-type > .vui-badge > img { width:48px; height:48px; border-radius:5px; }
</style>


